.navbar {
	margin-bottom: 0;
}

.nav {
	.avatar {
		width: 48px;
		height: 48px;
		position: absolute;
		top: 12px;
		left: -50px;
		border: 3px solid #333;
		border-radius: 50%;
	}
}

.navbar li a {
	padding: 25px 10px;
	transition: background-color 0.5s ease;
}

.navbar-nav li.dropdown ul li a {
	padding: 10px 10px;
	margin: 0;
}

.navbar-auth {
	margin-right: 32px;
}

.navbar-links {

	border-top: 1px solid #3c3c3c;

	ul {
		background-color: #3c3c3c;
		text-align: center;
	
		.navbar-link {
			background-color: #333;
			margin-right: 1px;
		}

		.navbar-link:first-child {
			margin-left: 1px;
		}

		.navbar-link ul li {
			text-align: left;
			margin: 0;
		}
	}
}


.dropdown-menu {
	width: 100%;
	overflow: hidden;
}

li.dropdown:hover .dropdown-toggle {
	background-color: #272727;
}

/* Brand */

.navbar {

	.navbar-brand {
		font-size: 180%;
		padding: 20px 0 0 40px;
		height: 70px;
	}

	.navbar-brand small {
		position: relative;
		left: -80px;
		top: 14px;
	}

}

/* Search Bar */

#search {
	margin-top: 15px;

	input {
		height: 40px;
		border-radius: 20px;
		background-color: #3c3c3c;
		border-width: 0;
		color: #ccc;
		padding: 0 20px;
	}

	.btn {
		z-index: 5;
		height: 40px;
		border-radius: 20px;
		border-width: 0;
		background: transparent;
		position: relative;
		right: 52px;
		color: #c3c3c3;
		transition: color 0.5s ease;
	}
}

#search:hover {
	.btn {
		color: #fff;
	}
}

/* Breadcrumbs */

.breadcrumb {
	background-color: #000c0f;
	padding: 30px 20px;
	color: #fff;
	font-size: 110%;
	margin-bottom: 0;
	border-radius: 0;
	border-width: 0;
	box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.25);
	li.active {
		color: #fff;
		font-weight: 500;
	}

	li {

		a {
			color: #fff;
			cursor: hand;
			text-decoration: none;
			padding: 3px 6px;
			border-radius: 5px;
			font-weight: 700;
			transition: background-color 1s ease;
			transition: color 0.5s ease;
		}

		a:hover {
			background-color: #eee;
			color: #333;
		}
		
	}
	
}


@media screen and (max-width: 766px) {

	.nav .dropdown-menu {
		display: block;
	}

	.nav .dropdown-menu li {
		width: 100%;
	}

}